import test from 'ava';
const server = require('../server.js');

const WebSocket = require('ws');  
const ws = new WebSocket('ws://localhost:8080'); 

ws.on('open', function open() {
    console.log('server is connected');

  test('connection', t => {      
      if(ws.readyState == 1){t.pass();}
      else{t.fail();}
  })


});
